#root {
	max-width: 960px;
	margin: 0 auto;
}

#root header {
	padding: 1rem 0;
}

#root header a {
	text-decoration: none;
	font-size: 3rem;
}

@media all and (min-width: 500px) {
	#root {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
}
